@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
* {
  font-family: Poppins;
}

html {
  background: url(/intro-component-with-signup-form-master/static/media/leaf-pattern-background.b14d83ae.jpg) no-repeat center center fixed;
  background-color: #9dffb0;
}

a:link, a:visited, a:active {
  color: #c48d3f;
  font-weight: 700;
  text-decoration: none;
}

a:hover {
  color: hsl(249, 10%, 26%);
  font-weight: 800;
}

input:focus, textarea:focus, select:focus {
  outline: none;
}
.Login {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  color: #fbfeb2;
}
.Left {
    text-align: left;
    height: 400px;
    width: 500px;
    margin: 150px 50px 10px 40px;
}

#learn {
    font-size: 1.25cm;
    text-align: left;
}

.leftcontent {
    text-align: left;
    font-size: 16px;
}
.login {
    background-color: #FFF;
    border-radius: 8px;
    box-shadow: 0 8px 1px rgba(0, 0, 0, 0.158);
    padding: 25px;
    text-align: center;
}


input, select, textarea {
    margin: 10px;
    border: hsl(246, 39%, 85%);
    border: 2px solid #f0f0f0;
    border-radius: 6px;
    width: 90%;
    padding: 15px;
    font-weight: 600;
}

input:hover, input:focus, textarea:focus, select:focus {
    border: 2px solid hsl(246, 25%, 77%);
}

.error {
    background-image: url(/intro-component-with-signup-form-master/static/media/icon-error.b72f792e.svg);
    background-repeat: no-repeat;
    background-position: right;
    background-position-x: 98%;
    background-color: #fff;
    border: 2px solid hsl(0, 100%, 74%);
    color: hsl(0, 100%, 74%);
}

input.error::-webkit-input-placeholder {
    color: #fff;
}
input.error:hover, input.error:focus {
    border: 2px solid  hsl(0, 47%, 49%);
}

button {
    background-color: #03d5e0;
    color: white;
    width: 97%;
    padding: 15px;
    margin: 10px;
    border: none;
    border-radius: 6px;
    box-shadow: inset 0 -5px 1px #03bbc5;
    font-weight: 500;
}

button:hover {
    font-weight: 800;
}

button:active {
    background-color:#81f1f7;
    box-shadow: inset 0 -5px 1px #03d5e0;
}

#oops {
    color: hsl(0, 100%, 74%);
    font-size: 10px;
    font-style: italic;
    text-align: right;
    margin: -5px 5px 0px -5px;
}

.disclaimer {
    font-size: 9px;
    color: #afafaf;
  }

.right {
    margin: 100px 50px 50px 50px;
    -webkit-align-self: center;
            align-self: center;
    width: 500px;
  }
  
  .tryit {
    text-align: center;
    background-color: #c48d3f;
    border-radius: 8px;
    box-shadow: 0 8px 1px rgba(0, 0, 0, 0.158);
    padding: 2px;
  }
.Main {
    margin: 50px 50px 20px 50px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-gap: 50px 50px;
    gap: 50px 50px;
    grid-template-areas:
        "Punchy Punchy Vladimir Midge"
        "Maddie Sherb Sherb Midge"
        ". . . .";
}

@media only screen and (max-width: 700px) {
    .Main {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
    }
}
#box {
    border-radius: 8px;
    box-shadow: 0 8px 1px rgba(0, 0, 0, 0.158);
    padding: 25px;
}

#avatar {
    border-radius: 30px;
    border: solid;
    height: 50px;
    width: 50px;
    float: left;
    margin: 10px 30px 15px 15px;
    background-color: black;
}

#user h3 {
    margin: -5px 0px -15px 0px;
}

#user p {
    font-size: 12px;
}

.Punchy { 
    grid-area: Punchy; 
    background: url(/intro-component-with-signup-form-master/static/media/catchfish.3e2b2142.png) 90% 90% no-repeat; 
    background-size: 25%;
}

.Vladimir { 
    grid-area: Vladimir; 
}

.Midge { 
    grid-area: Midge; 
    background: url(/intro-component-with-signup-form-master/static/media/palmtree.2a806830.png) 50% 98% no-repeat; 
    background-size: 90%;
}

.Maddie { 
    grid-area: Maddie; 
}

.Sherb { 
    grid-area: Sherb; 
}

